function notify(e, a) {
    ga('send', {
        hitType: 'event',
        eventCategory: e,
        eventAction: a,
        eventLabel: window.location
    });
}
const scene = document.querySelector('a-scene');
if (scene)
    ['enter', 'exit'].forEach(e =>
        ['ar', 'vr'].forEach(a =>
            scene.addEventListener(e + "-" + a, _ => notify(e, a))
        )
    )